<template>
  <div class="login">
    <van-nav-bar title="登录" left-arrow @click-left="goPage('My')"></van-nav-bar>

    <van-form> 
      <van-field
        v-model="userInfo.phone"
        label-width="1.5rem"
        placeholder="(请输入11位手机号)"
        maxlength="11"
        type="text"
        label="手机号"
        colon
      />

      <van-field
        v-model="code"
        center
        clearable
        label="验证码"
        colon
        label-width="2rem"
        placeholder="(请输入验证码)"
        label-align="right"
      >
        <template #button>
          <van-button @click="refreshCode">
            <Sidentify :identifyCode="identifyCode"></Sidentify>
          </van-button>
        </template>
      </van-field>

      <van-field
        v-model="userInfo.password"
        type="text"
        label="密码"
        colon
        label-width="50px"
        placeholder="(请输入6位以上数字或字母)"
        maxlength="16"
        :type="isEye ? 'text' : 'password'"
        :right-icon="isEye ? 'eye-o' : 'closed-eye'"
        @click-right-icon="PasswordEye"
      />

      <div class="register-btn">
        <van-button block type="info" @click="login" color="black" native-type="submit">登录</van-button>
      </div>
    </van-form>

    <div class="title">
      <div class="title_nav fl">
        <a href="javascript:;" @click="goPage('Forgot')" class="fr">忘记密码 ?</a>
      </div>
      <i class="line fl"></i>
      <div class="title_nav fl">
        <a href="javascript:;" @click="goPage('Register')">立即注册 ?</a>
      </div>
    </div>

    <div class="bottom">
      <img src="../assets/images/qq.png" alt srcset />
      <img src="../assets/images/微信.png" alt srcset />
      <img src="../assets/images/新浪.png" alt srcset />
      <div class="bottom_nav">
        <div class="anotherlogin fl">其他登录方式</div>
        <van-icon name="arrow-up" class="fl" size="16px" color="rgb(143, 143, 143)" />
      </div>
    </div>
  </div>
</template>

<script>
import "../assets/css/login.less";

//导入验证表单模块
import validForm from "../assets/js/validForm";
//导入验证码模块
import Sidentify from "../components/Sidentify.vue";

export default {
  components: {
    Sidentify
  },
  data() {
    return {
      checked: false,
      //用户注册信息
      userInfo: {
        phone: "",
        password: ""
      },
      //false: 闭合，true：睁开
      isEye: false,
      identifyCodes: "1234567890",
      identifyCode: "",
      code: ""
    };
  },

  methods: {
    //切换密码的格式
    PasswordEye() {
      this.isEye = !this.isEye;
    },

    //跳转页面
    goPage(name) {
      this.$router.push({ name });
    },
    //登录
    login() {
      //检验验证码是否正确
      if (this.code == "") {
        this.$toast("请输入验证码");
        return;
      } else if (this.identifyCode !== this.code) {
        this.code = "";
        this.refreshCode();
        this.$toast("请输入正确的验证码");
        return;
      } else {
        //进行验证表单
        if (validForm.validUserForm(this.userInfo)) {
          //复制对象
          let data = Object.assign({}, this.userInfo);

          //请求密钥
          data.appkey = this.appkey;

          //开启加载提示
          this.$toast.loading({
            message: "加载中...",
            forbidClick: true,

            //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
            duration: 0
          });

          //发起注册请求
          this.axios({
            method: "POST",
            url: "/login",

            //post传递参数需要写在data
            data
          })
            .then(result => {
              //关闭加载提示
              this.$toast.clear();
              
              if (result.data.code == 200) {
                //跳转到菜单
                

                //存储token，用于后面登录验证
                localStorage.setItem("__Tk", result.data.token);
              this.$router.push({name: 'Menu'});
              } else {
                this.$toast(result.data.msg);
              }
            })
            .catch(err => {
              //关闭加载提示
              this.$toast.clear();
              
            });
        }
      }
    },
    //验证码
    randomNum(min, max) {
      return Math.floor(Math.random() * (max - min) + min);
    },

    refreshCode() {
      this.identifyCode = "";
      this.makeCode(this.identifyCodes, 4);
    },
    makeCode(o, l) {
      for (let i = 0; i < l; i++) {
        this.identifyCode += this.identifyCodes[
          this.randomNum(0, this.identifyCodes.length)
        ];
      }
      
    },
  },
  created() {
    //初始化验证码
    this.refreshCode();
  },
  mounted() {
    this.identifyCode = "";
    this.makeCode(this.identifyCodes, 4);
  }
};
</script>

<style lang="less" scoped>
</style>